.auth__main {
    display: flex;
}

.auth__left-content {
    background: linear-gradient(138.61deg, #7F7FD5 1.24%, #86A8E7 50.74%, #91EAE4 98.97%);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 50vw;
    
}

.auth__svg {
}

.auth__right-content {
    height: 100vh;
    width: 50vw;
}

.auth__right-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 2rem;
    margin-right: 2rem;
    margin-top: 2rem;
    margin-bottom: 2rem;
}

.auth__body {
    margin-left: 2rem;
}

.auth__title {
    color: $black;
    font-size: 2.5rem;
    margin-bottom: 4rem;
    margin-top: 2rem;
}

.auth__form {
    margin-left: 1.5rem;
    margin-right: 2rem;
}

.auth__input-wrapper {
    // Esto actua como el display block, supongo que es más dinámico
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(2, 1fr);
    margin-bottom: 1rem;
}

.auth__label {
    color: $black;
    font-weight: 600;
    padding-bottom: 0.4rem;
}

.auth__input {
    margin-right: 1rem;
    height: 1.8rem;
    border: 0.06rem solid $light_gray;
    border-radius: 0.3rem;
    color: $black;
    font-size: 0.9rem;

    &:focus {
        border: 0.06rem solid $gray;
        outline: none;
    }
}

.auth__input-checkbox {
    margin-bottom: 1rem;
}

.auth__checkbox {
    margin-right: 0.5rem;
    cursor: pointer;

    &:focus {
        border: 0.06rem solid $gray;
        outline: none;
    }
}

.auth__label-checkbox {
    font-weight: 400;
}

.auth__form-button {
    color: $white;
    cursor: pointer;
    background-color: $blue;
    // Esto actua como el display block, supongo que es más dinámico
    border: none;
    border-radius: 0.3125rem;
    display: grid;
    font-size: 1rem;
    font-weight: 400;
    grid-template-columns: repeat(1, 1fr);
    margin-right: 0;
    padding-bottom: 0.375rem;
    padding-top: 0.375rem;
    width: 100%;

    &:focus {
        border: 0.06rem solid darken($blue, 40);
        outline: none;
    }
}

.auth__forgot-password {
    display: flex;
    justify-content: center;
    margin-top: 1.375rem;
}

.auth__forgot-password-link {
    align-content: center;
    color: $blue;
    text-decoration: none;
    
    &:focus {
        border: 0.06rem solid $gray;
        outline: none;
    }
}
