.main-content {
    display: flex;
}

.menu__sidebar {
    background-color: $black;
    color: $white;
    display: flex;
    flex-direction: column;
    height: 100vh;
    // Para que el sidebar se quede pegado al hacer scroll
    position: -webkit-sticky;
    position: sticky;
    top:0px;
    width: 24rem;
}

.divider-sidebar {
    display: block;
    border: 0;
    border-top: 0.1rem solid $dark_gray;
}

.title {
    display: flex;
    justify-content: left;
    padding-left: 1.2rem;
}

.menu__anchor { 
    color: $white;
    text-decoration: none;
}

.fa-graduation-cap {
    font-size: 1.4rem;
}

.menu__options-title {
    color: $light_gray;
    font-weight: 500;
    margin-top: 1.5rem;
    padding-left: 1.2rem;
}

.menu__options {
    flex: 1 1 auto;
    margin-top: 1rem;
    position: relative;
    // WEBKIT
    overflow-y: auto;
    overflow: auto;

}

.menu__option {
    cursor: pointer;
    padding-top: 0.8rem;
    padding-bottom: 0.8rem;
    margin-bottom: 0.4rem;
    display: flex;
    overflow: hidden;
    position: relative;

    &:hover {
        background-color: $blue;
        margin-left: 0.5rem;
        margin-right: 0.5rem;
        border-radius: 0.5rem;
        background-color: darken($color: $light_gray, $amount: 20);

            .menu__option-text {
                padding-left: 0.75rem;
            }
    }
}

.menu__option-text {
    padding-left: 1.25rem;
    font-weight: 500;
}

// Estilos cargados cuando está activado una opción
.option-active {
    background-color: $blue;
    margin-left: 0.5rem;
    margin-right: 0.5rem;
    border-radius: 0.5rem;

    .menu__option-text {
        padding-left: 0.75rem;
    }
}

.menu__contendedor-datos-usuario {
    cursor: pointer;
    margin-top: auto;
    margin-bottom: 4rem;
    width: 100%;
}

.menu__tarjeta_usuario {
    display: flex;
    padding-top: 1.25rem;
    padding-bottom: 1.25rem;
}

.menu__img-usuario {
    height: 40px;
    padding-left: 1rem;
    width: 40px;
}

.menu__nombre-posicion {
    padding-left: 1rem;
}

.menu__nombre-usuario {
    color: $white;
    font-weight: 500;
    font-size: 0.875rem;
}

.menu__posicion-usuario {
    color: $gray;
    font-size: 0.875rem;
}

// MODAL
.modal-background {
    background-color: rgba(0, 0, 0, 0.3);
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    position: fixed;
}

/*
 * DROPDOWN
 */

.dropright {
    display: flex;
}

.dropdown {
    display: flex;
    position: absolute;
    left: 18.7em;
    bottom: 2rem;
    width: 25rem;
}

.dropdown::after {
    z-index: 2;
}

.no-display {
    display: none;
}

.dropdown-triangle {
    width: 0; 
    height: 0; 
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-right:10px solid $white;
    margin-top: 15%;
}

.menu__dropdown-menu {
    background-color: $white;
    border-radius: 0.625rem;
    padding-top: 0.4rem;
}

.menu__dropdown-greet {
    color: $black;
    font-weight: 500;
    padding-left: 1.563rem;
    padding-bottom: 0.2rem;
    padding-top: 0.6rem;
    padding-right: 2rem;
}

.menu__dropdown-position {
    color: $dark_gray;
    font-weight: 500;
    padding-left: 1.563rem;
    padding-bottom: 0.2rem;
    padding-top: 0.6rem;
}

.menu__dropdown-edit {
    color: $blue;
    font-weight: 500;
    padding-left: 1.563rem;
    padding-bottom: 0.1rem;
    padding-top: 0.6rem;
}

.menu__dropdown-logout {
    color: $red;
    font-weight: 500;
    padding-left: 1.563rem;
    padding-bottom: 0.6rem;
    padding-top: 0.6rem;
}

.menu__dropdown-hr {
    height: 0.0625rem;
    background-color: $gray;
    border: none;
    margin-top: 0.2rem;
    margin-bottom: 0.2rem;
    margin-left: 0.6rem;
    margin-right: 0.6rem;
}
