.form__main-content {
    display: flex;
    flex-direction: column;
    height: 100%;
    overflow: auto;
    overflow: initial;
}

.filter div{
    filter: blur(0.08rem);
}

.filter {
    position: relative;
    z-index: -1;
}

.filter::after {
    position: absolute; 
    content: ''; 
    display: block; 
    top: 0; left: 0; 
    height: 100%; width: 100%;
    background: rgba(0, 0, 0, 0.4);
    mix-blend-mode: multiply;
    z-index: 1;
}

.form__form-title {
    padding-top: 1.3rem;
    padding-left: 1.3rem;
    padding-bottom: 1.15rem;
}

.form-title {
    color: $dark_gray;
    font-size: 1.2rem;
    font-weight: 500;
}

// Two columns wrapper
.form__form-body{
    display: flex;
    align-items: flex-start;
}

// Description wrapper
.form__form-description {
    border: 0.06rem solid $light_gray;
    border-radius: 0.5rem;
    height: auto;
    margin-left: 1rem;
    margin-top: 1rem;
    padding-bottom: 1.875rem;
    position: -webkit-sticky;
    position: sticky;
    top: 2rem;
}

.form__form-description-text {
    color: $gray;
    font-size: 1rem;
    font-weight: 400;
    padding: 0.8rem;
    padding-top: 1.8rem;
    padding-bottom: 1.8rem;
}

.divider-sidebar-form {
    display: block;
    border: 0;
    border-top: 0.06rem solid $light_gray;
}

// Submenu wrapper
.form__submenu {
    height: 9rem;
    padding-top: 1.5rem;
    // WEBKIT
    overflow-y: auto;
    overflow: auto;
}

// Submenu text
.form__subitem {
    color: $dark_gray;
    cursor: pointer;
    font-weight: 500;
    padding-left: 0.8rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    margin-bottom: 0.5rem;
    overflow: hidden;

    &:hover {
        background: rgba(47, 128, 237, 0.2);
        border-left: 0.2rem solid;
        border-left-color: $blue;
        color: $blue;
        padding-left: 0.6rem;
    }

}

.form__subitem-selected {
    background: rgba(47, 128, 237, 0.2);
    border-left: 0.2rem solid;
    border-left-color: $blue;
    color: $blue;
    padding-left: 0.6rem;
}

// FormDetails whole wrapper
.form__form-details {
    border: 0.06rem solid $light_gray;
    border-radius: 0.5rem;
    flex: 1 auto;
    margin-left: 1rem;
    margin-top: 1rem;
    margin-right: 1.4rem;
    padding-bottom: 1.875rem;
    margin-bottom: 1.875rem;
}

.form_form-details-wrapper {
    padding-bottom: 1.2rem;
    padding-left: 1.6rem;
    padding-top: 1.4rem;
    padding-right: 2rem;
}

.form_form-details-title {
    color: $dark_gray;
    font-size: 1.2rem;
    font-weight: 500;
    padding-bottom: 1rem;
}

.form__form-details-description {
    color: $gray;
}

// Wrapper del formulario, se divide en dos columnas
.form__form-wrapper {
    //Un display grid para restringir a dos columnas
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1rem;
    grid-auto-rows: minmax(1rem, auto);
    padding-left: 1.6rem;
    padding-right: 1.6rem;
    padding-top: 1.4rem;
    padding-bottom: 1.4rem;    
}

// Wrapper del input con label, se divide en 2 filas, 1 columna
.form__input-wrapper {
    // Esto actua como el display block, supongo que es más dinámico
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(2, 1fr);
}

.form__label {
    color: $dark_gray;
    text-transform: uppercase;
    font-weight: 400;
    padding-bottom: 0.4rem;
}

.form__input {
    margin-right: 1rem;
    height: 1.5rem;
    border: 0.06rem solid $light_gray;
    border-radius: 0.3rem;
    color: $black;
    font-size: 0.9rem;

    &:focus {
        border: 0.06rem solid $gray;
        outline: none;
    }
}

.form__button {
    background-color: $green;
    border: none;
    border-radius: 0.2rem;
    color: $white;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 500;
    margin-left: 1.5rem;
    margin-top: 1.8rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    padding-left: 2rem;
    padding-right: 2rem;
    transition: background-color 0.5s;

    &:hover {
        background-color: darken($green, 10);
    }
}

.dropzone-wrapper {
    background: $white;
    border: 0.2rem dashed $light_green;
    box-sizing: border-box;
    border-radius: 0.3125rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2.688rem;
    margin-right: 2.688rem;
    margin-top: 0.9375rem;
    margin-bottom: 0.9375rem;
}

.dropzone-content-wrapper {
    
    padding-top: 1.813rem;
    padding-bottom: 2.375rem;
}

.dropzone-content {
    color: $dark_gray;
    font-size: 1rem;
    font-weight: 500;
}

.dropzone-button {
    color: $dark_gray;
    cursor: pointer;
    font-weight: 500;
    background-color: $gray_button;
    border: none;
    border-radius: 0.3125rem;
    margin-left: 25%;
    margin-top: 1.563rem;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    padding-left: 1.063rem;
    padding-right: 1.063rem;
}
